body {
	transition: padding 400ms;
}

.push-down {
	padding-top: 38px;
}

.admin-bar-container {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	margin: 0;
	z-index: 1000;
}

.admin-bar-container .admin-bar-control {
	position: absolute;
	top: 0;
	right: 0;
	padding: 0 8px;
	height: 38px;
	line-height: 38px;
	background-color: #c33;
	color: #33cccc;
	font-size: 18px;
	text-align: center;
}

.admin-bar-container .admin-bar-control:hover {
	text-decoration: none;
	font-weight: bolder;
	color: #ddd;
}

.admin-bar-container .admin-bar-toggler {
	opacity: 0.5;
	border-bottom-left-radius: 1rem;
}

.admin-bar-container .admin-bar-inner {
	display: block;
	width: 100%;
	height: 38px;
	background-color: #c33;
	position: relative;
}

.admin-bar-container .hmenu {
	margin: 0;
	padding: 1px 10px;
	width: auto;
	list-style: none;
}

.admin-bar-container .hmenu li {
	float: left;
	display: block;
	margin-left: 4px;
	text-align: center;
	position: relative;
	line-height: 36px;
}

.admin-bar-container .hmenu li .dropdown {
	margin-right: -500px;
	line-height: normal;
}

.admin-bar-container .hmenu li .dropdown p {
	display: block;
	margin-top: 1em;
}

.admin-bar-container .hmenu li .menu-item {
	padding: 10px;
	color: #ccc;
	text-transform: uppercase;
	text-decoration: none;
	text-shadow: 0 1px 0 #000;
}

.admin-bar-container .hmenu li .btn,
.admin-bar-container .hmenu li .menu-item {
	font: bold 1.2em Arial, Helvetica;
}

.admin-bar-container .hmenu li .btn {
	margin: 5px 0 5px 5px;
}

.admin-bar-container .hmenu li .dropped,
.admin-bar-container .hmenu li:hover > .menu-item {
	color: #333;
	background-color: #fff;
	border-radius: 5px;
}

.admin-bar-container .hmenu li .dropped,
.admin-bar-container .hmenu li:hover > .dropped {
	padding-bottom: 14px;
	border-radius: 5px 5px 0 0;
	z-index: 1;
}

.admin-bar-container .hmenu li .dropdown {
	display: none;
	margin-top: 36px;
	margin-left: -6px;
	padding: 10px;
	background-color: #fff;
	border: 1px solid #c33;
	border-top: 0;
	position: absolute;
	top: 0;
	text-align: left;
	border-radius: 5px;
}

.admin-bar-container .hmenu li .spacing {
	width: 25px;
}

.admin-bar-container .hmenu .right {
	float: right;
	position: relative;
}

.admin-bar-container .blocks-gallery {
	margin: 0;
	padding: 0;
	height: 160px;
	overflow: auto;
}

.admin-bar-container .blocks-gallery .grid {
	margin-right: 8px;
}

.sitemaker-block {
	text-align: center;
	font-weight: bolder;
	cursor: move;
}

.sitemaker-block .inner {
	margin: 5px 0;
	padding: 10px;
	border: 1px solid #ccc;
	border-radius: 5px;
	background-color: #f0faf4;
}

.sitemaker-block .inner:hover {
	background-color: #1d9c73;
	color: #fff;
	opacity: 0.8;
}

.sitemaker-block i {
	float: left;
	color: #1d9c73;
}

.sitemaker-block:hover i {
	color: #000;
}

.block-controls {
	display: none;
	position: absolute;
	top: 15px;
	right: 15px;
	width: auto;
	z-index: 2000;
}

.forabg + .block-controls {
	top: 7px;
	right: 4px;
}

.block-receiver {
	min-height: 55px;
	overflow: visible;
}

.block-receiver .block-controls .item-action {
	float: right;
}

.block-receiver .block {
	position: relative;
}

.block-receiver .block .inline-form {
	display: inline;
}

.block-receiver .block .inline-form input {
	width: 65%;
	font-size: 0.7rem;
	cursor: pointer;
	cursor: hand;
}

.block-receiver .block .editable {
	overflow: hidden;
}

.block-receiver .block .editable:hover {
	outline: 1px dashed inverse;
}

.block-receiver .block:hover > .block-controls {
	display: block;
}

.block-receiver .block:hover > .block-controls a:hover {
	border: 1px solid #ccc;
}

.block-receiver .block-title {
	height: 20px;
	width: auto;
	max-width: 90%;
	cursor: pointer;
	overflow: visible;
}

.block-receiver .block-icon {
	display: inline-block;
	min-width: 15px;
	min-height: 10px;
	padding: 1px;
	margin-left: 0.1rem;
	text-align: center;
	cursor: pointer;
	outline: 1px solid #ddd;
}

.block-receiver .block-icon:hover {
	outline: 1px solid #968787;
}

.block-receiver .placeholder {
	display: block;
	position: relative;
	height: 100px;
	width: 100%;
}

.block-receiver.horizontal .sortable,
.block-receiver.horizontal .placeholder {
	float: left;
	height: 70px;
	margin-bottom: 10px;
	overflow: hidden;
}

.block-receiver.horizontal .placeholder {
	margin-left: 10px;
	width: 15px;
}

.block-receiver.horizontal:after {
	content: ' ';
	display: block;
	height: 0;
	clear: both;
}

.block-receiver.show-position {
	outline: 1px dashed #ccc;
}

.block-receiver.show-position.sidebar {
	min-height: 450px;
}

.block-receiver.empty-position {
	display: none;
}

.dragging {
	white-space: nowrap;
	width: 150px;
}

.modifiers {
	overflow: auto;
	margin-top: 0;
	display: none;
}

.modifiers dl {
	display: block;
	height: auto;
}

.modifiers .scroll-list {
	position: relative;
	height: 100px;
	overflow: auto;
	padding-right: 3px;
}

.block-admin-button-active {
	background: #ffffff none;
	border-bottom: 0;
	z-index: 1000;
}

.dialog-check-button {
	float: left;
	display: block;
	margin-right: 5px;
	margin-top: 1em;
}

.block-class-actions {
	margin-left: 5px;
}

.message-container {
	position: relative;
	left: 50%;
	float: left;
	clear: both;
	font-size: 1.2em;
	text-align: left;
}

.message-container .flash-message {
	display: none;
	background-color: #d5f8a0;
	padding: 5px 10px;
	position: relative;
	left: -50%;
	text-align: left;
}

.block-settings {
	padding-left: 0;
	padding-right: 0;
}

.block-settings fieldset textarea {
	width: 97%;
}

.sm-inactive,
.sm-inactive > .block,
.sm-responsive-show,
.sm-responsive-hide {
	opacity: 0.5;
}

.block-class-editor {
	width: 99%;
	outline: 1px solid #666;
	height: 40px;
	overflow: auto;
}